.Button, .Button:visited {
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  font-family: inherit;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  cursor: pointer;
  color: #f0f0f0;
  text-shadow: none;
  overflow: hidden;
  border: none;
  background-color: #27adfa; }

.Button:hover {
  background-color: #e6902f;
  -webkit-transform: translateY(-0.1rem);
      -ms-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem); }

.Button:focus {
  outline: none; }

.Button:active {
  outline: none;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0); }

.Link {
  text-decoration: none;
  color: #e6902f; }

.large {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: bold; }

.medium {
  padding: 1rem 2rem;
  font-size: 1.2rem;
  font-weight: bold; }

.small {
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 300; }



