.Container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  text-align: center; }
  .Container::-webkit-scrollbar-track {
    -webkit-box-shadow: 0 0 6px rgba(35, 35, 35, 0.5) inset;
            box-shadow: 0 0 6px rgba(35, 35, 35, 0.5) inset;
    background-color: #505050; }
  .Container::-webkit-scrollbar {
    width: 12px;
    background-color: #505050; }
  .Container::-webkit-scrollbar-thumb {
    -webkit-box-shadow: 0 0 6px rgba(35, 35, 35, 0.5) inset;
            box-shadow: 0 0 6px rgba(35, 35, 35, 0.5) inset;
    background-color: #e6902f; }



