.Toolbar {
  width: 100%;
  height: 20vh;
  position: relative;
  z-index: 100; }
  @media only screen and (min-height: 500px) {
    .Toolbar {
      height: 15vh; } }
  @media only screen and (min-height: 768px) {
    .Toolbar {
      height: 10vh; } }
  .Toolbar__menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    height: 50%;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#505050), color-stop(5%, #232323), color-stop(95%, #232323), to(#505050));
    background-image: -webkit-linear-gradient(#505050, #232323 5%, #232323 95%, #505050);
    background-image: -o-linear-gradient(#505050, #232323 5%, #232323 95%, #505050);
    background-image: linear-gradient(#505050, #232323 5%, #232323 95%, #505050);
    -webkit-box-shadow: 0 -0.15rem 0.5rem rgba(0, 0, 0, 0.5), 0 0.5rem 1rem rgba(0, 0, 0, 0.6);
            box-shadow: 0 -0.15rem 0.5rem rgba(0, 0, 0, 0.5), 0 0.5rem 1rem rgba(0, 0, 0, 0.6); }

@media (max-width: 1023px) {
  .desktopOnly {
    display: none; } }



