.Modal {
  position: fixed;
  z-index: 500;
  width: 80%;
  height: 400px;
  -webkit-box-shadow: 0 -0.15rem 0.5rem rgba(0, 0, 0, 0.5), 0 0.5rem 1rem rgba(0, 0, 0, 0.6);
          box-shadow: 0 -0.15rem 0.5rem rgba(0, 0, 0, 0.5), 0 0.5rem 1rem rgba(0, 0, 0, 0.6);
  color: #232323;
  left: 10%;
  top: 5%;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out; }

@media (min-width: 450px) and (min-height: 320px) {
  .Modal {
    height: 300px; } }

@media (min-height: 500px) {
  .Modal {
    height: 400px; } }

@media (min-width: 700px) and (min-height: 700px) {
  .Modal {
    width: 70%;
    left: 15%; } }



